
<template>
<div id="mplay">
  <audio :src="currSong" autoplay="" controls="" @ended="nextSong"></audio>
  <ul>
    <li v-for="(item,index) in songs">
      <button @click="play(index)">{{ item.song }}</button>
      <p>{{ item.author }}</p>
    </li>
  </ul>


</div>
</template>

<script>
  export default {
    name: 'play',
    data(){return {
      //defaultSong:"../static/muscis/.mp3",
      indexSong:0,
      songs:[
        {song:".mp3",author:"",src:"../static/muscis/.mp3"},
        {song:".mp3",author:"",src:"../static/muscis/.mp3"},
        {song:".mp3",author:"",src:"../static/muscis/.mp3"},
        {song:".mp3",author:"",src:"../static/muscis/.mp3"},
        {song:".mp3",author:"",src:"../static/muscis/.mp3"},
        {song:".mp3",author:"",src:"../static/muscis/.mp3"},

      ]
    }},
    methods:{
      play(index){
        this.indexSong=index
      },
      nextSong(){
        if (indexSong==songs.length-1){
          this.indexSong=-1;
        }
        this.indexSong++;
        //this.defaultSong=this.songs[this.indexSong].src;
      },
      addSong(){
        this.songs.push({song:".mp3",author:"",src:"../static/muscis/.mp3"});
      }
    },
    computed:{
      //
      currSong(){
        console.log(111);
        return this.songs[this.indexSong].src
      }
    }

  }
</script>



